































































































@use "~main/theme"

.tunnel-iframe
    width: 100%
    height: 100vh
    background: rgb(var(--color-white))
    position: absolute
    top: 0
    left: 0
    padding-top: 52px
    padding-bottom: 68px

.floating-panel
  position: fixed
  bottom: 15px
  right: 15px
  box-shadow: 2px 2px 10px rgba(0,0,0,.3)
  background-color: rgb(var(--color-body-bg))
  padding: 10px
  max-width: 300px
  z-index: 10
